import React from 'react';
import PropTypes from 'prop-types';

import { colors, BORDER_RADIUS, PADDING_UNIT } from '../config/style-guide';

const PostCard = ({ cover, children }) => (
    <div className="postCard">
        <style jsx>{`
            .postCard {
                color: white;
                max-width: 600px;
                border-radius: ${BORDER_RADIUS}px;
            }

            .head, .body {
                height: 200px;
                overflow: hidden;

                transition: 200ms all;
            }

            img, .head {
                width: 100%;
                border-radius: ${BORDER_RADIUS}px ${BORDER_RADIUS}px 0 0;
                display: block;
            }

            img {
                width: calc(100% - 60px);
                margin: 0 auto;
            }

            .head {
                background: ${colors.transparent.midlight};
            }

            .body {
                border: 2px solid ${colors.lightblue};
                background: ${colors.lightblue};
                border-top: none;
                border-radius: 0 0 ${BORDER_RADIUS}px ${BORDER_RADIUS}px;
            }

            .postCard:hover .head {
                background: ${colors.transparent.light};
            }

        `}
        </style>
        <div className="head">
            <img src={cover} alt={cover} />
        </div>

        <div className="body">
            {children}
        </div>
    </div>
);

PostCard.propTypes = {
    cover: PropTypes.string.isRequired,
    children: PropTypes.node.isRequired,
};

export default PostCard;
